// TimelineCentered.tsx
import type { TimelineItem, TimelineStage } from "./timeline";

function TimelineCard({ item }: { item: TimelineItem }) {
  return (
    <div className="tCard">
      <div className="tYear">{item.year}</div>
      <div className="tTitle">{item.title}</div>
      {item.subtitle ? <div className="tSub">{item.subtitle}</div> : null}

      <p className="tBody">{item.body}</p>

      {item.tags?.length ? (
        <div className="tTags">
          {item.tags.map((t) => (
            <span key={t} className="tTag">
              {t}
            </span>
          ))}
        </div>
      ) : null}

      {item.links?.length ? (
        <div className="tLinks">
          {item.links.map((l) => {
            const external = l.href.startsWith("http");
            return (
              <a
                key={l.href}
                className="tLink"
                href={l.href}
                target={external ? "_blank" : undefined}
                rel={external ? "noreferrer" : undefined}
              >
                {l.label} ‚Üí
              </a>
            );
          })}
        </div>
      ) : null}
    </div>
  );
}

function stageLabel(stage?: TimelineStage) {
  if (stage === "learning") return "Research";
  if (stage === "tools") return "Tooling";
  if (stage === "companies") return "Real systems";
  return "";
}

function stageClass(stage?: TimelineStage) {
  if (stage === "learning") return "tStageLearning";
  if (stage === "tools") return "tStageTools";
  if (stage === "companies") return "tStageCompanies";
  return "";
}

function StageLabelInline({ stage }: { stage?: TimelineStage }) {
  const label = stageLabel(stage);
  if (!label) return null;

  return (
    <div className={`tStageInline ${stageClass(stage)}`}>
      <div className="tStageInlineText">{label}</div>
    </div>
  );
}

export default function TimelineCentered({ items }: { items: TimelineItem[] }) {
  return (
    <section className="timelineCentered">
      {items.map((item, idx) => {
        const side = idx % 2 === 0 ? "left" : "right";

        const prevStage = idx > 0 ? items[idx - 1]?.stage : undefined;
        const isStageStart = item.stage && item.stage !== prevStage;

        return (
          <div key={`${item.year}-${item.title}`}>
            {isStageStart ? <StageLabelInline stage={item.stage} /> : null}

            <div className={`tRowCentered ${stageClass(item.stage)}`}>
              <div className={`tSide tSideLeft ${side === "left" ? "show" : ""}`}>
                {side === "left" ? <TimelineCard item={item} /> : null}
              </div>

              <div className="tMiddle">
                <div className="tDot" />
                {idx !== items.length - 1 ? <div className="tLine" /> : null}
              </div>

              <div className={`tSide tSideRight ${side === "right" ? "show" : ""}`}>
                {side === "right" ? <TimelineCard item={item} /> : null}
              </div>
            </div>
          </div>
        );
      })}
    </section>
  );
}
