// LandingProjects.tsx

import Link from "next/link";
import { projects } from "@/app/projects/data";

type PillTone = "companies" | "simulation" | "research";

export default function LandingProjects() {
  const pillToneClass: Record<PillTone, string> = {
    companies: "pillCompanies",
    simulation: "pillSimulation",
    research: "pillResearch",
  };

  return (
    <div className="grid">
      {projects.map((p) => (
        <Link
          key={p.slug}
          href={`/projects/${p.slug}`}
          className="card"
          scroll={true}
        >
          <div className="cardTop">
            <div className="cardHead">
              <div className="cardTitle">{p.title}</div>
              <div className="cardSub">{p.subtitle}</div>
              <div className="cardRole">{p.role}</div>
            </div>

            <div className={`pill ${pillToneClass[p.pillTone]}`}>
              {p.pill}
            </div>
          </div>

          <p className="cardDesc">{p.desc}</p>

          <div className="cardLink">
            {p.comingSoon ? "Coming soon ‚Üí" : "View ‚Üí"}
          </div>
        </Link>
      ))}
    </div>
  );
}
